import React from 'react'
import './row-style.css'
import  {firestore as db} from '../../firebase'
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";

export const Rows = (props)=>{

        // <img className="image" src={`https://robohash.org/${props.cat.id}?set=set2&size=180x180`}/>
    
       const removefromdb = async (event)=> { 

               console.log(props.index);

        
                try{
                    event.preventDefault();
        
                    let val = props.index;
                    
                    db.collection("users").doc(props.uid).get()
                    .then(result=>{
                        if(result.exists){
        
        
        
                            let todo = (result.data()).todo;
                            
                            todo.splice(val,1);
        
                            db.collection("users").doc(props.uid).update(
                                {
                                    todo: todo
                                }
                            )
                        }
        
                    })
                    
                    
        
        
                }catch(erro){
        
                }
        
            };

            const handleClick = (e, data)=>{

               let pos = prompt("enter new pos");
               pos--;

               try{
               
    
                
                
                db.collection("users").doc(props.uid).get()
                .then(result=>{
                    if(result.exists){
    
    
    
                        let todo = (result.data()).todo;
                        //rearrange todo//
                        let currPos = props.index;

                        if(pos>todo.length || pos <=0){
                            alert("Insert proper size value");
                            return;
                        }

                        if(currPos > pos){

                            let currData = todo[currPos];

                            //put item on top
                            for(let i=currPos; i>pos; i--){
                            
                                todo[i] = todo[i-1];
                            }
                            todo[pos] = currData;
                        }
                        else{
                            
                            let currData = todo[currPos];
                            //put item below
                            for(let i=currPos+1; i<=pos; i++){
                            
                                todo[i-1] = todo[i];
                            }
                            todo[pos] = currData;


                        }


                        
    
                        db.collection("users").doc(props.uid).update(
                            {
                                todo: todo
                            }
                        )
                    }
    
                })
                
                
    
    
            }catch(erro){
    
            }
            }





                return (
                <div className="rrows">


                
                
                        
                    <p id="text">{props.cat}</p>
                    
                    <button onClick = {(event)=> removefromdb(event)}> <i class="fa" style={{fontSize: "24px"}}> &#xf014; </i> </button >

                    <ContextMenuTrigger id={props.index} holdToDisplay={1000}>
                        <div><i class="fa fa-bars" aria-hidden="true" style={{fontSize: "24px", padding:"12px"}}></i></div>
                    </ContextMenuTrigger>

                    <ContextMenu className="menuBody" id={props.index} >
                        <MenuItem onClick={(events)=>handleClick(events)} className="menuItem">Change position</MenuItem>
                    </ContextMenu>
                       
                                
                        
                </div>
                );
       
                               
                                        
                       
        
    
}


